  --secondary-text-color: #939393;
  --accent-color: #dc56c5;
  --button-color: #4c00fe;
  --button-hover-color: #2e009a;
  --primary-bg-color: #0e0e0e;
  --search-form-bg-color: #fff3;
  --overlay-color: #0b0b0b80;
  --overlay-gradient-prymary-color: #0d0d0d00;
  --overlay-gradient-secondary-color: #0e0e0ecc;
  --backdrop-bg-color: #200d1d99;
  --modal-primary-bg-color: #fff2fd;
  --modal-secondary-color: #c4c4c4;
